import React, { useEffect } from 'react';
import { useRouter } from 'next/router';
import { EntryActionsTypes } from '../../containers/entries/show';
import { State } from '../../reducers';

interface EntriesListProps {}

interface WpPostProps {
  id: number;
  date: string;
  slug: string;
  status: string;
  title: {
    rendered: string;
  };
  _embedded: any;
}

const Entry: React.FC<State & EntriesListProps & EntryActionsTypes> = ({
  wpPost,
  getWpPostBySlug
}) => {
  const router = useRouter();
  const { slug } = router.query;
  useEffect(() => {
    console.log(wpPost);
    if (typeof slug !== 'string') return;
    getWpPostBySlug(slug);
    console.log(wpPost);
  }, []);

  if (wpPost.data) {
    const { title }: WpPostProps = wpPost.data;
    return <div className="entryList">{title.rendered}</div>;
  }

  return <div className="error">404</div>;
};

export default Entry;
